html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}

@-webkit-keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  20%,
  80% {
    -webkit-transform: translate3d(14px, 0, 0);
            transform: translate3d(14px, 0, 0);
  }
  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  40%,
  60% {
    -webkit-transform: translate3d(14px, 0, 0);
            transform: translate3d(14px, 0, 0);
  }
}

@keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  20%,
  80% {
    -webkit-transform: translate3d(14px, 0, 0);
            transform: translate3d(14px, 0, 0);
  }
  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  40%,
  60% {
    -webkit-transform: translate3d(14px, 0, 0);
            transform: translate3d(14px, 0, 0);
  }
}

.shake {
  -webkit-animation: shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.clicky-game {
  background-image: url(data:image/png;base64,);
  background-repeat: repeat;
}

@-webkit-keyframes correct {
  0% {
    color: #fff;
  }
  50% {
    color: #2e7d32;
  }
  to {
    color: #fff;
  }
}

@keyframes correct {
  0% {
    color: #fff;
  }
  50% {
    color: #2e7d32;
  }
  to {
    color: #fff;
  }
}

@-webkit-keyframes incorrect {
  0% {
    color: #fff;
  }
  50% {
    color: #ff6563;
  }
  to {
    color: #fff;
  }
}

@keyframes incorrect {
  0% {
    color: #fff;
  }
  50% {
    color: #ff6563;
  }
  to {
    color: #fff;
  }
}

.navbar {
  background-color: #630100;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  font-size: calc(10px + 14 * (100vw - 320px) / 800);
  margin-bottom: 0;
  min-height: 5rem;
  z-index: 99;
}

a.navbar-brand {
  font-size: calc(10px + 14 * (100vw - 320px) / 800);
  font-weight: 700;
}

.navbar .user-message {
  color: #ffffff;
}

.navbar .correct {
  -webkit-animation: 0.4s correct;
          animation: 0.4s correct;
}

.navbar .incorrect {
  -webkit-animation: 0.4s incorrect;
          animation: 0.4s incorrect;
}

.jumbotron {
  background-color: #7c0100;
  background-image: url(/clicky-game/static/media/05-01.dcfe0319.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  height: 25rem;
  padding: 9.5rem 1.25rem 8.25rem 1.25rem;
  position: relative;
  width: 100%;
}

.caption p.instructions {
  font-size: 1.575rem;
}

.game-container {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  padding-bottom: 1.5625rem;
}

.card-flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  padding-bottom: 1rem;
  padding-top: 1rem;
}

.card {
  cursor: pointer;
  margin: 1rem 1.25rem;
}

.card .img-container {
  /* Scale the background image */
  background-position: 50%;
  /* Disable the background image repeat */
  background-repeat: no-repeat;
  /* Set the background image to contain to normalise all of the sizes */
  background-size: contain;
  /* Use the Gossamer image to normalise the image dimensions (height and width) */
  height: 180px;
  width: 185px;
  /* Create interior space in the card */
  margin: 1rem;
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: 0.1s;
  transition: 0.1s;
}

.footer {
  background-color: #7c0100;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  height: 5rem;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width: 100%;
}

.footer .container-fluid {
  padding-left: 0;
  padding-right: 0;
}

.footer .container-fluid p {
  background: #630100;
  font-weight: 600;
  margin: 0;
  padding: 5px 5px 5px 10px;
  width: 100%;
}

.react-logo {
  width: 30px;
}

